.footer{height:150px;display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;background-color:#000}.lienFooter{display:flex;justify-content:center}.linkedinFooter{margin:0 10px 0 10px;width:52px;height:52px;transition:transform 0.3s ease}.linkedinFooter:hover{transform:scale(1.1)}.gitFooter{margin:0 10px 0 10px;width:50px;height:50px;transition:transform 0.3s ease}.gitFooter:hover{transform:scale(1.1)}.pFooter{color:#fff;font-size:16px}@media screen and (max-width:991px){.pfooter{font-size:12px}}